@charset "utf-8";
body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}

body {
	line-height: 1;
	font-family: "Montserrat", "Nanum Gothic",  "sans-serif";
	font-size: 1em;
	background-color: #000;
	transition: background-color 0.8s ease-in-out;
	overflow-x: hidden;
}

body.white {
	background-color: #fff;
}

body.fixed{
	overflow-x: hidden;
	overflow-y: hidden;
}

/* main */
#main {
	position: relative;
	height: 100vh;
}

/* header */
#header{
	position: absolute;
	top: 0;
	height: 100px;
	width: 100%;
	z-index: 999;
	transition: transform 0.4s;
}

#header.active{
	position: fixed;
	transform: translateY(0);
}


#header .inner{
	position: relative;
	margin: 0 6vw;
	height: 100px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

#header h1 .logo {
	display: block;
	width: 120px;
	height: 60px;
	text-indent: -999px;
	overflow: hidden;
	background: url(./images/logo_white.svg) no-repeat center center;
	background-size: 120px auto;
}

body.white #header h1 .logo {
	display: block;
	width: 120px;
	height: 60px;
	text-indent: -999px;
	overflow: hidden;
	background: url(./images/logo_black.svg) no-repeat center center;
	background-size: 120px auto;
}

/* gnb */
#gnb {
	padding-right: 11vw;
}
#gnb ul {
	display: flex;
	justify-content: center;
	align-items: center;
}

#gnb ul li a{
	display: block;
	position: relative;
	color: #fff;
	font-weight: 600;
	font-size: 1.13em;
	padding: 0 20px;
	line-height: 100px;
}

body.white #gnb ul li a {
	color: #272727;
}


#gnb ul li:hover a,
#gnb ul li.on a {
	color: #FFB7CA;
}

body.white #gnb ul li:hover ,
body.white #gnb ul li.on a {
	color: #FFB7CA;
}


/* tab */
#tab {
	display: block;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 0;
	width: 2em;
	height: 1em;
	cursor: pointer;
	z-index: 999;
	/* transition: all 0.3s ease-in-out; */
}

#tab .tab_line {
	position: absolute;
	right: 0;
    display: inline-block;
    height: 2px;
    background: #fff;
    transition: all 0.3s ease-in-out;
}

body.white #tab .tab_line {
	background: #272727;
}


#tab .tab_line.top{
	top: 0;
	width: 2em;
}

#tab .tab_line.bottom{
	top: 8px;
	width: 1em;
	z-index: 999;
}

#tab.active .tab_line{
	background-color: #fff;
}

#tab.active .tab_line.top{
	transform: rotate(45deg);
}

#tab.active .tab_line.bottom{
	top: 0px;
	width: 2em;
	transform: rotate(-45deg);
}

body.white #tab.active .tab_line {
	background: #fff;
}


#main .content {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	gap: 12vh;
}

#main .content .text_box .inner {
	margin: 0 14vw;
}



#main .content .text_box .inner span.top {
	font-family: "Poppins";
	display: block;
	font-size: 6vw;
	font-weight: 700;
	-webkit-text-stroke-width: 2px;
	-webkit-text-stroke-color: #FFB7CA; 
	color: transparent;

}

#main .content .text_box .inner span.middle {
	display: block;
	font-size: 6vw;
	font-weight: 700;
	color: #FFB7CA;
	margin-top: 20px;
}

#main .content .text_box .inner span.bottom{
	font-size: 6vw;
	font-weight: 700;
	color: #FFB7CA;
	margin-left: 23.5vw;
}


#main .content .skill_box .inner {
	margin: 0 14vw;
}

#right_move {
	opacity: 0;
	transform: translateX(-100px);
	transition: opacity 2s, transform 2s;
	transition-delay: .5s;
}

#right_move.active {
	opacity: 1;
	transform: translateX(0);

}


#main .content .skill_box .inner ul {
	display: flex;
	justify-content: space-between;
	gap: 2em;
}

/* #main .content .skill_box .inner ul li{
	width: calc((100% - 12em)/5);
} */


#main .content .skill_box .inner ul a {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 15px;
	/* height: 210px; */
}

#main .content .skill_box .inner ul a strong {
	display: block;
	color: #fff;
	font-size: 1.25em;
	font-weight: 700;
}

#main .content .skill_box .inner ul a span.line {
	display: inline-block;
	width: 10px;
	height: 2px;
	background-color: #fff;
}

#main .content .skill_box .inner ul a span.cont {
	display: block;
	color: #fff;
	margin: 5px 0;
	font-weight: 300;
	font-size: 0.88em;
	line-height: 1.5em;
	white-space: nowrap;
}

#main .content .skill_box .inner ul a span.num {
	display: block;
	color: #fff;
	font-weight: 300;
	font-size: 0.88em;
}

#main .resume {
	position: absolute;
	right: 6vw;
	top: 50%;
	transform: translateY(-50%);
	display: block;
	height: 100px;
}

#main .resume svg {
	display: block;
	width: 100px;
	height: 100px;
	overflow: visible;
	transform: rotate(-90deg);
}

#main .resume svg circle {
	fill: none;
	stroke: #fff;
	stroke-width: 1px;
	stroke-dasharray: 314.159; 
	stroke-dashoffset: 314.159; 
}

#main .resume svg circle.active {
	animation: stroke_animation 3s 1s alternate infinite;
	animation-fill-mode: forwards;
}

#main .resume .circle_back {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: block;
	width: 100px;
	height: 100px;
	stroke-width: 1px;
	border-radius: 50%;
	background-color: transparent;
	border: 1px solid #fff;
	opacity: .5;
}

#main .resume .inner {
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}


#main .resume .inner strong {
	display: block;
	color: #fff;
	font-size: 0.88em;
	font-weight: 400;
}

#main .resume .inner span.line {
	display: inline-block;
	width: 10px;
	height: 1px;
	background-color: #fff;
}

@keyframes stroke_animation {
	from {
		stroke-dashoffset: 314.159;
	}
	to {
		stroke-dashoffset: 0;
	}
}


/* sitemap */
#sitemap {
	z-index: 100;
	width: 100vw;
	height: 100vh;
	background-color: #000;
	position: fixed;
	left: 0;
	top: 0;
	transition: transform cubic-bezier(0.77, 0, 0.175, 1) 0.8s;
    transform: translateX(0%) translateY(-100%);
	overflow: hidden;
}

#sitemap.active {
	transform: translateX(0%) translateY(0%)
}

#mobile {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}


#mobile ul {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 6vh;
}

#mobile ul li a {
	display: block;
	font-size: 6vh;
	font-weight: 600;
	color: #fff;
}

#mobile ul li:hover a,
#mobile ul li:focus a {
	color: #FFB7CA;
}

/* bg */
.bg {
	position: relative;
	width: 100%;
	height: 100vh;
	z-index: -1;
}

#main .bg video {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

@media only screen and (max-width: 1200px) {
	#main .content .text_box .inner span.top {
		font-size: 4.75em;
	}
	
	#main .content .text_box .inner span.middle {
		font-size: 4.75em;
	}
	
	#main .content .text_box .inner span.bottom{
		font-size: 4.75em;
	}

	#main .content .text_box.active span.top{
		font-size: 3.75em;
	
	}

	#main .content .text_box.active span.middle{
		font-size: 3.75em;
	
	}

	#main .content .text_box.active span.bottom{
		font-size: 3.75em;
	
	}
	
}

@media only screen and (max-width: 1020px) {
	#main .content .skill_box .inner ul a span.cont {
		display: none;
	}

	#main .content .skill_box .inner ul a {
		justify-content: flex-start;
		height: auto;
	}
	
	
}

@media only screen and (max-width: 950px) {
	#gnb {
		display: none;
	}

	#main .content {
		width: 100%;
		height: 100%;
		align-items: center;
	}
	
	#main .content .skill_box {
		display: none;
	}
	#main .resume {
		top: auto;
		transform: translateY(0);
		bottom: 6em;
	}

	#main .content .text_box .inner {
		margin: 0;
	}
	

}

@media only screen and (max-width: 760px) {
	#main .content .text_box .inner span.bottom{
		margin-left: 0;
	}

	#main .resume {
		top: auto;
		transform: translateY(0);
		right: auto;
		left: 50%;
		transform: translateX(-50%);
	}

}


@media only screen and (max-width: 590px) {
	#main .content .text_box .inner span.top {
		font-size: 13vw;
	}
	
	#main .content .text_box .inner span.middle {
		font-size: 13vw;
	}
	
	#main .content .text_box .inner span.bottom{
		font-size: 13vw;
	} 

	#mobile ul li a {
		font-size: 10vw;
	}

}

/* section1 */
section{
	padding: 25vh 0;
	width: 100%;
}

section .inner{
	margin: 0 6vw;
}

section .inner .title strong {
	display: block;
	font-size: 2.5em;
	color: #272727;
}

section .inner .title span.line {
	display: inline-block;
	width: 4em;
	height: 1px;
	background-color: #272727;
}


#section1 .inner .text_box {
	max-width: 1400px;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;
	margin: 5em 0;
	padding: 0 10vw;
}

#section1 .inner .text_box .right_box {
	display: flex;
	flex-direction: column;
	gap: 13em;
}

#section1 .inner .text_box .right_box .top{
	display: flex;
	flex-direction: row;
	gap: 4em;
}

#section1 .inner .text_box .right_box .top .name_box {
	white-space: nowrap;
}

#section1 .inner .text_box .right_box .top .name_box strong{
	display: block;
	/* font-size: 5vw; */
	font-size: 5em;
	color: #fff;
	font-weight: 600;
	animation: gradient 8s linear infinite alternate;
	background: linear-gradient(90deg, rgb(255, 255, 255) 0%, 
	rgb(252, 183, 202) 25%, 
	rgb(255, 255, 255) 75%, 
	rgb(247, 236, 103) 100%);
	background-size: 1000% 100%;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

@keyframes gradient{
	0% {
		background-position: 0% 50%;
	}
	100% {
		background-position: 100% 50%;
	}
}

body.white #section1 .inner .text_box .right_box .top .name_box strong {
	color: #272727;
}

#section1 .inner .text_box .right_box .bottom p {
	line-height: 2em;
	font-size: 1.06em;
	letter-spacing: 0.8px;
}

#section1 .inner .text_box .right_box .bottom span{
	display: block;
	color: #fff;
	word-break: keep-all;
}

body.white #section1 .inner .text_box .right_box .bottom span{
	color: #272727;
}

@media only screen and (max-width: 1200px) {
	#section1 .inner .text_box .right_box .top .name_box.active strong{
		font-size: 4em;
	
	}
}


@media only screen and (max-width: 950px) {
	#section1 .inner .text_box {
		flex-direction: column;
		padding: 0;
	}


}

@media only screen and (max-width: 720px) {
	#section1 .inner .text_box .right_box .top{
		flex-direction: column;
	}
	

}

@media only screen and (max-width: 670px) {
	#section1 .inner .text_box .right_box .top .name_box {
		white-space: wrap;
	}
	#section1 .inner .text_box .right_box .bottom span{
		display: inline;
	}
	
	
}


/* section2 */
#section2 {
	overflow: hidden;
}

#section2 .inner .skill_slider {
	position: relative;
	max-width: 1400px;
	margin: 0 auto;
	height: 300px;
	margin-top: 6em;
	padding-bottom: 10em;
}

#section2 .inner .skill_slider .swiper{
	position: static;
	overflow: visible;
}

#section2 .inner .skill_slider .swiper-container {
	width: 100%;
	height: 100%;
}

#section2 .inner .skill_slider .swiper-slide {
	display: flex;
	flex-direction: column;
	gap: 50px;
}


#section2 .inner .skill_slider .swiper-slide .skill strong {
	display: block;
	color: #272727;
	font-size: 1.25em;
	font-weight: 700;
}

#section2 .inner .skill_slider .swiper-slide .skill span.line {
	display: inline-block;
	width: 10px;
	height: 2px;
	background-color: #272727;
}

#section2 .inner .skill_slider .swiper-slide .icon_wrap ul {
	display: flex;
	align-items: center;
	gap: 10px;
}

#section2 .inner .skill_slider .swiper-slide .desc {
	line-height: 1.5em;
}

#section2 .inner .skill_slider .swiper-slide .desc p span{
	display: block;
	font-size: 0.88em;
}

#section2 .inner .skill_slider .swiper-scrollbar {
	width: 80vw;
	left: 0;
	bottom: 7px;
	overflow: hidden;
}

#section2 .inner .skill_slider .swiper-scrollbar .swiper-scrollbar-drag {
	background: #272727;
	width: 40%;
}
#section2 .inner .skill_slider .swiper-scrollbar .swiper-scrollbar-drag::before {
	content: '';
    background-color: #272727;
    left: -99vw;
    top: 0;
    position: absolute;
    bottom: 0;
    height: 100%;
    width: 100vw;
    z-index: 1;
}



/* section3 */
#section3 .inner{
	position: relative;
	margin:  0;
	overflow: hidden;
}
#section3 .inner button {
	border: none;
	outline: none;
	background-color: inherit ;
	cursor: pointer;
	width: 100%;
}

#section3 .inner .text_box {
	white-space: nowrap;
}

#section3 .inner .text_box strong{
	display: block;
	text-align: left;
	color: #FFB7CA;
	font-size: 10vw;
	margin-left: 6vw;
}

body.white #section3 .inner .text_box strong {
	color: #272727;
}

#section3 .inner .text_box span{
	font-family: "Poppins";
	text-align: center;
	display: block;
	font-size: 9vw;
	font-weight: 700;
	-webkit-text-stroke-width: 2px;
	-webkit-text-stroke-color: #FFB7CA; 
	color: transparent;
	margin-top: 7vw;
}

body.white #section3 .inner .text_box span {
	-webkit-text-stroke-color: #272727; 
}

#section3 .inner .track {
	display: flex;
	position: absolute;
	z-index: 90;
	right: 0;
	width: 100%;
	top: 50%;
	transform: translateY(-50%);
}
#section3 .inner .track .track_text ul {
	display: flex;
	gap: 20px;

}

#section3 .inner .track .track_text ul li span{
	line-height: 1;
	white-space: nowrap;
	font-weight: 700;
	opacity: 0.3;
	color: #FFB7CA;
	font-size: 5vw;
}

body.white #section3 .inner .track .track_text ul li span {
	color: #272727; 
}

@media only screen and (max-width: 1200px) {
	#section3 .inner .text_box strong{
		font-size: 7.50em;
	}

	#section3 .inner .text_box span{
		font-size: 7.50em;
	}

	#section3 .inner .track .track_text ul li span{
		font-size: 3.75em;
	}
	
}


@media only screen and (max-width: 700px) {
	#section3 .inner .text_box strong{
		font-size: 16vw;
	}

	#section3 .inner .text_box span{
		font-size: 16vw;
	}

	#section3 .inner .track .track_text ul li span{
		font-size: 8vw;
	}

}

