		display: flex;
		align-items: flex-start;
		flex-direction: column;
		height: 14em;
	}

	#website #sub_section1 .inner .list ul .content .center_box{
		width: 65vw;
		padding-right: 0;
	}
	

}

@media only screen and (max-width: 720px) {
#website #sub_section1 .inner .list ul .content {
	height: 29em;
}

#website #sub_section1 .inner .list ul .content .center_box{
	width: 80vw;
}
	
#website #sub_section1 .inner .list ul .content .right_box .site_more {
	right: auto;
	left: 0;
	top: auto;
	transform: translateY(0);
	bottom: 0;
	margin-bottom: 1em;
}


}


/* sub section2 */

#website #sub_section2 .inner .open_slider {
	position: relative;
	max-width: 1400px;
	margin: 0 auto;
	height: 28em;
	padding: 6em 0;
	overflow: hidden;
}

#website #sub_section2 .inner .open_slider .swiper{
	position: static;
